/**
 * Zoom Control Component
 */

// Dependencies
import React from 'react';

const ZoomControls = () => {
	return (
		<div className='zoomControl__container hidden__print'>
			<button>Zoom In</button>
			<button>Zoom Out</button>
			<button>Reset</button>
		</div>
	);
};

export default ZoomControls;
